import { useState, useEffect } from "react";
import {
  Descriptions,
  Card,
} from "antd";
import DocumentApi from "api/document-api";
import moment from "moment";
import FileList from "views/app-views/shared/files";

const dateFormat = "DD-MM-YYYY";

const Overview = (props) => {
    const [item, setItem] = useState(props.item);    
  
    useEffect(() => {
        let api = new DocumentApi();
        api.get(parseInt(item.id)).then((response) => {
            console.log(response);
            setItem(props.item);
        });
    }, [item.id, props.item]);  

  return (
    item && (
      <>
        <Card>
        {item && (
          <Descriptions bordered column={2}>
            <Descriptions.Item label="S hiu VB" span={2}>
                {item.documentCode}
            </Descriptions.Item>
            <Descriptions.Item label="Trch yu" span={2}>
                {item.summary}
            </Descriptions.Item>
            <Descriptions.Item label="File nh km" span={2}>
                {item.fileModels && (
                    <FileList viewOnly={true} files={item.fileModels} />
                )}
            </Descriptions.Item>
            <Descriptions.Item label="Loi VB" span={2}>
                {item.documentTypeName}
            </Descriptions.Item>
            <Descriptions.Item label="Ch " >
                {item.topicName}
            </Descriptions.Item>
            <Descriptions.Item label="Lnh vc"  >
                {item.areaName}
            </Descriptions.Item>
            <Descriptions.Item label="C quan ban hnh"  >
                {item.organizationName} - ngy {moment(item.issuedDate).format(dateFormat)}
            </Descriptions.Item>
            <Descriptions.Item label="Ngi k"  >
                {item.signer} - {item.positionName}
            </Descriptions.Item>
            <Descriptions.Item label="Ngy ng cng bo"  >
                {moment(item.annoucementDate).format(dateFormat)}- trong {item.annouceNumber} ngy
            </Descriptions.Item>
            <Descriptions.Item label="Ngy ht hiu lc" >
                {moment(item.expirationDate).format(dateFormat)}- trong {item.expireNumber} ngy
            </Descriptions.Item>
            <Descriptions.Item label="Trng thi"  >
                {item.documentStatusName}
            </Descriptions.Item>            
          </Descriptions>
        )}
      </Card>
      </>
    )
  );
};

export default Overview;
