import { useState, useEffect } from "react";
import {
  Input,
  Row,
  Col,
  Card,
  Form,
  message,
  Button,
  DatePicker,
  Select,
} from "antd";
import { CloudUploadOutlined } from "@ant-design/icons";
import Flex from "components/shared-components/Flex";
import { useHistory } from "react-router-dom";
import DocumentApi from "api/document-api";
import CatalogSelector from "views/app-views/shared/catalogSelector";
import { CatalogType } from "types/CatalogType";
import moment from "moment";
import FileList from "views/app-views/shared/files";

const dateFormat = "DD-MM-YYYY";
const { Option } = Select;

const GeneralField = (props) => {
  const [item, setItem] = useState(props.item);
  if (item.documentStatus == null) {
    item.documentStatus = 0;
  }

  const [submitLoading, setSubmitLoading] = useState(false);

  let history = useHistory();

  const handleChange = (event) => {
    const currentItem = Object.assign({}, item);
    const targetValue =
      event.target.type === "checkbox"
        ? event.target.checked
        : event.target.value;
    currentItem[event.target.name] = targetValue;
    setItem({ ...currentItem });
  };

  const handleDateChange = (fieldName, value) => {
    const currentItem = Object.assign({}, item);
    currentItem[fieldName] = value;
    setItem({ ...currentItem });
  };

  const hangeCatalogChange = (fieldName, value) => {
    const currentItem = Object.assign({}, item);
    currentItem[fieldName] = value;
    setItem({ ...currentItem });
  };

  const onFinish = () => {
    setSubmitLoading(true);
    new DocumentApi().save(item).then((response) => {
      setSubmitLoading(false);
      if (item.id === 0) {
        message.success(`To mi thnh cng.`);
        history.push(`/lms/modules/myoffice/document/edit/${response.data.id}`);
      }
      if (item.id > 0) {
        message.success(`Cp nht thnh cng.`);
      }
    });
  };

  const handleSelectChange = (field, value) => {
    const currentItem = Object.assign({}, item);
    currentItem[field] = value;
    setItem({ ...currentItem });
    if (props.handleChange) {
      props.handleChange(currentItem);
    }
  };

  const removeFile = (file) => {
    item.fileModels = item.fileModels.filter((x) => x.url != file.url);
    item["files"] = JSON.stringify(item.fileModels);
    setItem({ ...item });
  };

  const selectFiles = () => {
    window.CKFinder.modal({
      chooseFiles: true,
      width: 800,
      height: 600,
      onInit: function (finder) {
        finder.on("files:choose", function (evt) {
          const currentItem = Object.assign({}, item);

          evt.data.files.map((file) => {
            const fileModel = {
              name: file.attributes.name,
              extension: file._extenstion,
              size: file.attributes.size,
              url: file.attributes.url,
            };
            currentItem.fileModels.push(fileModel);
          });

          currentItem["files"] = JSON.stringify(currentItem.fileModels);
          setItem({ ...currentItem });
        });
      },
    });
  };

  useEffect(() => {
    setItem(props.item);
  }, [props.item]);

  return (
    item && (
      <>
        <Row gutter={16}>
          <Col span={24}>
            <Flex justifyContent="between">
              <div className="mb-3">&nbsp;</div>
              <div className="mb-3">
                <Button
                  type="primary"
                  onClick={() => onFinish()}
                  loading={submitLoading}
                >
                  Cp nht
                </Button>
              </div>
            </Flex>
          </Col>
          <Col xs={24} sm={24} md={24}>
            <Card title="Thng tin">              
              <Row gutter={16}>
                <Col span={12}>
                  <Form.Item label="S hiu vn bn">
                    <Input
                      name="documentCode"
                      value={item.documentCode}
                      onChange={handleChange}
                      defaultValue={props.item?.documentCode}
                    />
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item label="Loi vn bn">
                    <Select
                      placeholder="Chn loi vn bn"
                      value={item.documentType}
                      name="type"
                      onChange={(value) => handleSelectChange("documentType", value)}
                    >
                      <Option value={0}>Vn bn Ni b</Option>
                      <Option value={1}>Vn bn n</Option>
                      <Option value={2}>Vn bn i</Option>
                    </Select>
                  </Form.Item>
                </Col>
              </Row>
              <Row gutter={16}>
                <Col span={24}>
                  <Form.Item label="Trch yu">
                    <Input.TextArea
                      name="summary"
                      value={item.summary}
                      rows={4}
                      placeholder="Nhp trch yu"
                      onChange={handleChange}
                    />
                  </Form.Item>
                </Col>
              </Row>
              <Row gutter={16}>
                <Col span={12}>
                  <Form.Item label="Ch ">
                    <CatalogSelector
                      value={item.topicId}
                      onChange={(value) => hangeCatalogChange("topicId", value)}
                      type={CatalogType.DOCUMENT_TOPIC}
                    />
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item label="Lnh vc">
                    <CatalogSelector
                      value={item.areaId}
                      onChange={(value) => hangeCatalogChange("areaId", value)}
                      type={CatalogType.DOCUMENT_AREA}
                    />
                  </Form.Item>
                </Col>
              </Row>
              <Row gutter={16}>
                <Col span={12}>
                  <Form.Item label="C quan ban hnh">
                    <CatalogSelector
                      value={item.organizationId}
                      onChange={(value) =>
                        hangeCatalogChange("organizationId", value)
                      }
                      type={CatalogType.DOCUMENT_ORGANIZATION}
                    />
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item label="Ngy ban hnh">
                    {!item.issuedDate && (
                      <DatePicker
                        className="w-100"
                        placeholder="Chn ngy"
                        showTime={false}
                        onChange={(value, format) => {
                          handleDateChange("issuedDate", value);
                        }}
                        name="issuedDate"
                        format={dateFormat}
                      />
                    )}
                    {item.issuedDate && (
                      <DatePicker
                        className="w-100"
                        placeholder="Chn ngy"
                        showTime={false}
                        onChange={(value, format) => {
                          handleDateChange("issuedDate", value);
                        }}
                        name="issuedDate"
                        format={dateFormat}
                        value={moment(item.issuedDate)}
                      />
                    )}
                  </Form.Item>
                </Col>
              </Row>
              <Row gutter={16}>
                <Col span={12}>
                  <Form.Item label="Ngi k">
                    <CatalogSelector
                      value={item.signerId}
                      onChange={(value) =>
                        hangeCatalogChange("signerId", value)
                      }
                      type={CatalogType.DOCUMENT_SIGNER}
                    />
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item label="Chc v ngi k">
                    <CatalogSelector
                      value={item.positionId}
                      onChange={(value) =>
                        hangeCatalogChange("positionId", value)
                      }
                      type={CatalogType.DOCUMENT_POSITION}
                    />
                  </Form.Item>
                </Col>
              </Row>
              <Row gutter={16}>
                <Col span={6}>
                  <Form.Item label="Ngy ng cng bo">
                    {!item.annoucementDate && (
                      <DatePicker
                        className="w-100"
                        placeholder="Chn ngy"
                        showTime={false}
                        onChange={(value, format) => {
                          handleDateChange("annoucementDate", value);
                        }}
                        name="annoucementDate"
                        format={dateFormat}
                      />
                    )}
                    {item.annoucementDate && (
                      <DatePicker
                        className="w-100"
                        placeholder="Chn ngy"
                        showTime={false}
                        onChange={(value, format) => {
                          handleDateChange("annoucementDate", value);
                        }}
                        name="annoucementDate"
                        format={dateFormat}
                        value={moment(item.annoucementDate)}
                      />
                    )}
                  </Form.Item>
                </Col>
                <Col span={6}>
                  <Form.Item label="S ngy">
                    <Input
                      type="number"
                      name="annouceNumber"
                      value={item.annouceNumber}
                      placeholder="Hiu lc sau ngy ng cng bo"
                      onChange={handleChange}
                    />
                  </Form.Item>
                </Col>
                <Col span={6}>
                  <Form.Item label="Ngy ht hiu lc">
                    {!item.expirationDate && (
                      <DatePicker
                        className="w-100"
                        placeholder="Chn ngy"
                        showTime={false}
                        onChange={(value, format) => {
                          handleDateChange("expirationDate", value);
                        }}
                        name="expirationDate"
                        format={dateFormat}
                      />
                    )}
                    {item.expirationDate && (
                      <DatePicker
                        className="w-100"
                        placeholder="Chn ngy"
                        showTime={false}
                        onChange={(value, format) => {
                          handleDateChange("expirationDate", value);
                        }}
                        name="expirationDate"
                        format={dateFormat}
                        value={moment(item.expirationDate)}
                      />
                    )}
                  </Form.Item>
                </Col>
                <Col span={6}>
                  <Form.Item label="S ngy">
                    <Input
                      type="number"
                      name="expireNumber"
                      value={item.expireNumber}
                      placeholder="Ht hiu lc sau (s ngy)"
                      onChange={handleChange}
                    />
                  </Form.Item>
                </Col>
              </Row>
              <Row gutter={16}>
                {/* <Col span={6}>
                  <Form.Item label="S th t">
                    <Input
                      name="order"
                      type="number"
                      value={item.order}
                      onChange={handleChange}
                      defaultValue={props.item?.order}
                    />
                  </Form.Item>
                </Col> */}
                <Col span={6}>
                  <Form.Item label="Trng thi">
                    <Select
                      placeholder="Chn Trng thi"
                      defaultValue={item.documentStatus}
                      value={item.documentStatus}
                      name="documentStatus"
                      onChange={(value) =>
                        handleSelectChange("documentStatus", value)
                      }
                    >
                      <Option value={0}>Chn trng thi</Option>
                      <Option value={1}>Cn hiu lc</Option>
                      <Option value={2}>Ht hiu lc</Option>
                    </Select>
                  </Form.Item>
                </Col>
              </Row>
              <Form.Item label="File nh km">
                <Button
                  onClick={() => selectFiles()}
                  icon={<CloudUploadOutlined />}
                >
                  Chn file
                </Button>
                {item.fileModels && (
                  <FileList
                    removeFile={removeFile}
                    viewOnly={false}
                    files={item.fileModels}
                  />
                )}
              </Form.Item>
            </Card>
          </Col>
        </Row>
      </>
    )
  );
};

export default GeneralField;
